.footer {
    padding-top: 0;
    position: relative;
    background: url(../../assets//bg.png) center center /cover;
}

.footer .subscribe {
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 75%;
    margin: auto;
    padding: 30px;
    border: 0.5px solid rgb(39, 39, 39);
    gap: 50px;
    position: relative;
    z-index: 10;
    border-top: 0;

}

.footer::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-color: rgb(0, 0, 0);
}

.footer .subscribe div {
    width: 70%;
}

.footer .top {
    text-align: center;
}

.footer .top h2 {
    font-size: 37px;
}

.footer .subscribe .input-subscribe {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.footer .subscribe .input-subscribe input {
    padding: 10px 14px;
    border: 0;
    outline: 0;
    width: 75%;
    background-color: black;
    color: gray;
    border: 1px solid rgb(44, 44, 44);
    font-size: 19px;
}

@media(max-width:950px) {
    .footer .subscribe {
        width: 90%;
    }
}

@media(max-width:768px) {
    .footer .subscribe {
        width: 100%;
    }

    .footer .subscribe .input-subscribe {
        flex-direction: column;
    }

    .footer .subscribe .input-subscribe input {
        width: 100%;
    }

    .footer .subscribe .input-subscribe button {
        display: block;
        width: 100%;
    }
}

.footer-tail {}

.footer-tail .footer-tail-upper {
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    padding: 50px 0;
    text-align: center;
    gap: 50px;
    flex-wrap: wrap;
    align-items: start;
}

.footer-tail .footer-tail-upper>div {
    flex: 1 26%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.media {
    display: flex;
    gap: 20px;
    color: white;
    font-size: 17px;
}

.footer-tail .footer-tail-upper h2 {
    margin: auto;
}

.footer-tail .footer-tail-upper p {
    color: white;
}

.footer-tail>p {
    text-align: center;
}