@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;500;600&family=Montserrat:wght@300;400;500;600;700&family=Open+Sans:wght@300;400;500&family=Poppins:wght@100;200;300;400;500&family=Varela+Round&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;500;600&family=Cormorant+Upright:wght@300;400;700&family=Montserrat:wght@300;400;500;600;700&family=Open+Sans:wght@300;400;500&family=Poppins:wght@100;200;300;400;500&family=Varela+Round&display=swap');

body {
    font-family: 'open sans';
}

html {
    scroll-behavior: smooth;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    --webkit-box-sizing: border-box;
    --moz-box-sizing: border-box;
}

a {
    text-decoration: none;
    transition: 0.3s;
}


ul,
nav,
li {
    list-style: none;
}

.content {
    display: flex;
    gap: 50px;
    justify-content: space-between;
    align-items: center;

}

.content {
    display: flex;
    justify-content: space-between;
    width: 79%;
    align-items: center;
    gap: 100px;
    margin: auto;
}


@media(max-width:1200px) {
    .content {
        width: 100%;
        padding: 10px;
        gap: 50px;
    }
}

@media(max-width:768px) {
    .content {
        flex-direction: column;
    }
}

.btns {
    display: flex;
    gap: 10px;
    z-index: 10;
}

.btns button {
    border-radius: 7px;
    transition: background-color 0.3s;
    width: 60px;
    height: 40px;
}

button:hover {
    background-color: rgb(25, 100, 229);
    color: white;
}

.btns button:active {
    transform: scale(1.02);
}

button {
    transition: 0.3s;
    width: fit-content;
    border: 0;
    cursor: pointer;
    padding: 8px 14px;
    background-color: rgba(220, 202, 135, 1);
    font-family: 'Cormorant Upright';
    font-size: 17px;
    display: flex;
    justify-content: center;
    align-items: center;
}

h1,
h2,
h3,
h4 {
    color: rgba(220, 202, 135, 1);
}

p {
    color: rgb(189, 189, 189);
    line-height: 1.5;
}

section {
    padding: 50px;
    font-family: 'Cormorant Upright';
}

@media(max-width:768px) {
    section {
        padding: 20px;
    }
}

video {
    width: 100%;
    margin: auto
}

.video {
    position: relative;
}

.video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}

.icon-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: white;
    cursor: pointer;
    font-size: 22px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid rgba(220, 202, 135, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.3s;
    line-height: 100px;
}

.icon-container:hover {
    background-color: rgba(220, 202, 135, 1);
}

.play {
    width: 29px;
    height: 24px;
    margin-left: 4px;
}