.believe {
    background: url(../../assets/bg.png);
}


.believe .left {}

.believe .left img {
    width: 400px;
    height: 500px;
}

@media(max-width:500px) {
    .believe .left img {
        max-width: 100%;
    }
}

.believe .right {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.believe .right .top {}

.believe .right .top p {}

.believe .right .top img {
    width: 40px;
    height: 10px;
}

.believe .right .top h2 {}

.believe .right .texts {}

.believe .right .texts div {
    color: white;
}

.believe .right .texts div img {
    width: 47px;
    height: 40px;
}

.believe .right .texts p {}

.believe .right .info {}

.believe .right .info span {
    color: white;
}

.believe .right img {
    width: 200px;
    height: 70px;
}