header {
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
    align-items: center;
    background-color: rgb(18, 18, 18);
    color: white;
    height: 100px;
}

.logo {
    position: relative;
}

.logo h2 {
    user-select: none;
    position: relative;
    overflow: hidden;
    width: fit-content;
}

.logo h2::before {
    position: absolute;
    content: '';
    width: 25px;
    height: 25px;
    border-radius: 50%;
    font-size: 16px;
    background-color: rgb(255, 0, 153);
    color: white;
    left: -12px;
    top: -12px;
}

.logo h2::after {
    position: absolute;
    content: '';
    width: 25px;
    height: 25px;
    border-radius: 50%;
    font-size: 16px;
    background-color: rgb(255, 0, 153);
    color: white;
    right: -12px;
    top: -12px;
}

header .nav-links {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 17px;
    flex: 1;
}

header .nav-links li {}

header .nav-links li a {
    transition: 0.3s;

    color: rgb(240, 240, 240);
}

header .nav-links-small li a {
    color: white;
}

header .login {
    display: flex;
    gap: 15px;
    justify-content: space-between;
    align-items: center;
}

header .login a {
    color: white;
    transition: 0.3s;
}

header .login a:hover {
    color: gray;
}

header .login span {
    display: block;
    width: 1px;
    height: 30px;
    background-color: #DDDD;
}

.nav-links-small {
    height: 0;
    overflow: hidden;
    position: fixed;
    z-index: 10;
    left: 0;
    right: 0;
    top: 0px;
    bottom: 0;
    background-color: black;
    color: white;
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;
}

.nav-links-small li {
    transition: 0.3s;
    width: 140px;
}

.nav-links-small li a {
    display: block;
    width: 100%;
    padding: 4px 14px;
}

.back {
    cursor: pointer;
    position: absolute;
    top: 20px;
    left: 20px;
    width: 35px;
    height: 35px;
    background-color: rgb(75, 75, 75);
    font-size: 22px;
    padding: 2px;
    border-radius: 50%;
    transition: 0.3s;
}

.back:hover {
    background-color: gray;
}

.nav-links-small li:hover {
    margin-left: 7px;
}

nav li:hover a {
    color: gray;
}

.nav-links-small.show {
    height: calc(100vh);
}

@media(min-width:769px) {
    .nav-links-small.show {
        display: none;
    }
}

.fabars {
    font-size: 20px;
    color: white;
    cursor: pointer;
    display: none;
}

@media(max-width:768px) {
    header .nav-links {
        display: none;
    }

    .fabars {
        display: block;
    }


}

@media(max-width:500px) {
    header .login {
        display: none;
    }
}