.laurels {
    position: relative;
    background: url(../../assets//bg.png);
}

.laurels>img {
    position: absolute;
    left: 50px;
    top: 50px;
    height: 100px;
    width: 100px;
}

@media(max-width:768px) {
    .laurels>img {
        display: none;
    }
}

@media(max-width:1200px) {
    .laurels>img {
        left: 20px;
        top: 20px;
        height: 50px;
        width: 50px;
    }
}

.laurels .content {}

.laurels .content .left {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.laurels .content .left .grid {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: space-between;
}

.laurels .content .left .grid .grid-item {
    display: flex;
    gap: 10px;
    flex: 45%;
    align-items: center;
    width: 150px;
}

.laurels .content .left .grid .grid-item img {
    width: 60;
    height: 60px;
}

.laurels .content.left .grid .grid-item .grid-item-info {}

.laurels .content .right {}

.laurels .content .right img {
    height: 500px;
}

@media(max-width:500px) {
    .laurels .content .right img {
        max-width: 100%;
    }
}