.special {
    background-color: black;
}

.special .special-head {
    text-align: center;
    margin: 20px 0;
}

.special .special-head h6 {
    color: white;
}

.special .special-head h2 {
    font-size: 34px;
    color: rgb(233, 198, 135);
}

.special .special-content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    width: 75%;
    margin: auto;
    gap: 30px;
    align-items: center;
    justify-content: center;
}

.special-content-left {}

.special-content-right {}

.special-content-right h3,
.special-content-left h3 {
    text-align: center;
    font-size: 30px;
    margin: 15px 0;
    color: white;
}

.special-content-middle {}

@media(max-width:768px) {
    .special-content-middle {
        display: none;
    }
}

.special-content-middle img {
    width: 100%;
    height: 500px;
}

.wine,
.cokteil {

    display: flex;
    flex-direction: column;
    gap: 15px;
}

.cokteil div div,
.wine div div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}

.cokteil div div span:nth-child(1),
.wine div div span:nth-child(1) {
    font-weight: bold;
    font-size: 20px;
    color: rgb(241, 204, 123);
    flex: 1;

}

.cokteil div div span:nth-child(2),
.wine div div span:nth-child(2) {
    flex: 1;
    text-align: center;
}

.special button {
    display: block;
    margin: 15px auto;
    width: fit-content;
}