.hero {
    height: calc(100vh - 100px);
    background-color: rgb(18, 18, 18);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px;
    position: relative;
}

.hero .hero-content {
    display: flex;
    justify-content: space-between;
    width: 70%;
    align-items: center;
    gap: 120px;
}

@media(max-width:1200px) {
    .hero .hero-content {
        width: 100%;
        padding: 10px;
        gap: 50px;
    }
}

@media(max-width:768px) {
    .hero {
        height: fit-content;
    }

    .hero .hero-content {
        flex-direction: column;
    }
}

.hero .hero-content .hero-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
}

.hero .hero-content .hero-info .hero-info-top {}

.hero .hero-content .hero-info .hero-info-top h4 {
    color: rgb(230, 230, 230);
}

.hero .hero-content .hero-info .hero-info-top img {}

.hero .hero-content .hero-info h1 {
    color: rgba(220, 202, 135, 1);
    font-size: 50px;
    line-height: 50px;
}


.hero .hero-content .image-container img {
    width: 400px;
    height: 400px;
}

.float {
    position: absolute;
    right: 60px;
    bottom: 60px;
    color: rgba(220, 202, 135, 1);
    width: fit-content;
    font-size: 17px;
}

.float::before {
    position: absolute;
    content: '';
    height: 33px;
    width: 3px;
    border-radius: 2px;
    background-image: linear-gradient(to bottom, rgb(219, 198, 124), rgb(236, 225, 197));
    left: 50%;
    transform: translateX(-50%);
    top: -35px;
}