.gallery {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    background-color: black;
    align-items: center;
}

.gallery .left {
    flex: 1;
}

.gallery .title {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.gallery .swiper {
    flex: 3;
    justify-content: center;
}

.gallery .swiper img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    cursor: pointer;
    position: relative;
}

.gallery .swiper .slide .slide-overlay {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    width: 0%;
    z-index: 20;
    transition: 0.3s;
    height: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.gallery .swiper .slide .slide-overlay svg {
    font-size: 25px;
    cursor: pointer;
}

.gallery .swiper .slide .slide-overlay svg:hover {
    color: rgb(218, 201, 130);
}

.gallery .swiper .slide:hover .slide-overlay {
    height: 100%;
    width: 100%;
}


@media(max-width:950px) {
    .gallery {
        align-items: start;
        flex-direction: column;
    }

    .swiper {
        width: 100%;
    }
}