.about {
    background: url(../../assets/bg.png);
    display: flex;
    justify-content: center;
    position: relative;
}

.about .about-content {
    width: 70%;
    display: flex;
    gap: 40px;
    align-items: center;
    position: relative;
    z-index: 20;
}

@media(max-width:1200px) {
    .about .about-content {
        width: 100%;
        padding: 10px;
    }
}

@media(max-width:768px) {
    .about .about-content {
        flex-direction: column;
    }

    .about .about-content .about-middle {
        display: none;
    }
}


.about .about-content .about-middle img {
    width: 100px;
}

.about .about-content .about-left {
    text-align: right;
}

.about .about-content .about-left img {
    transform: rotateY(180deg);
}

.about .about-content .about-left button {
    margin-left: auto;
}

.about .about-content .about-left,
.about .about-content .about-right {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.back-img {
    position: absolute;
    /* left: 50%; */
    /* transform: translateX(-50%); */
    top: 50%;
    transform: translateY(-50%);
    width: 300px;
    height: 300px;
    opacity: 0.7;
    z-index: 10;
}